@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.767)), to(rgba(0, 0, 0, 0.767))), url(/train-live-status/static/media/background.0ecdb8aa.jpg);
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.767), rgba(0, 0, 0, 0.767)), url(/train-live-status/static/media/background.0ecdb8aa.jpg);
  background: linear-gradient(to right, rgba(0, 0, 0, 0.767), rgba(0, 0, 0, 0.767)), url(/train-live-status/static/media/background.0ecdb8aa.jpg);
  background-size: cover;
  height: 100vh;
  display: flex;
  /* justify-content: center; */
}

.section-trainstatus {
  margin-top: 150px;
}

input[type="text"] {
  width: 50%;
  height: 50px;
  border: 0;
  border-bottom: solid 2px #f16051;
  outline: none;
  background-color: transparent;
  margin-right: 20px;
  color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}

button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);  
}

button:active {
  outline: none;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.title-container__title {
  color: rgb(204, 204, 204) !important;
}

.title-container__subtitle {
  color: rgb(153, 153, 153) !important;
}

.details {
  color: #fff !important;
}

.__info {
  background-color: #2c3e50;
  border-radius: 8px;
  padding: 10px 5px 3px 5px;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
}

.__info hr {
  height: 1px;
  background-color: #f16051;
}

.__key {
  color: rgb(153, 153, 153) !important;
}

.__value {
  color: rgb(204, 204, 204) !important;
}

select {
  margin-right: 20px;
}

.form-container {
  margin-top: 5px;
}

.links {
  justify-content: end;
  margin-top: 20px;
}

.links a {
  margin-right: 10px;
  color: rgb(204, 204, 204) !important;
}

.active {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.my-custom-scrollbar {
  position: relative;
  height: 252px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}

.notice {
  background-color: #000000;
  color: #fff;
  padding: 5px;
  text-align: center;
}
